import type { Block, ValueToTextRule } from '@ali/4ever-cangjie';
import Card from '../../models';
import projectCard from './projectCard';
import calendar from './calendar';
import url from './url';
import link from './link';

const convertPlugins = {
  projectCard,
  calendar,
  url,
  link,
};

const valueToText: ValueToTextRule = {
  name: 'card',

  match(node) {
    return Card.isCard(node);
  },

  convert(node) {
    const { data } = (node as Block);
    const { cardType, metadata } = data;
    const { subject } = metadata || {};
    const targetConvert = convertPlugins[cardType];
    if (targetConvert) return targetConvert(data);

    return subject || '';
  },
};

export default valueToText;
